.nav__main {
    width: 60%;
}

.nav__main ul {
    list-style: none;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.nav__main ul li {
    position: relative;
    cursor: pointer;
    line-height: 1; /* */
}

.nav__main ul li:after {
    display: block;
    position: absolute;
    top: 25px;
    left: 0; /*  right:0;,       */
    width: 0;/*     */
    height: 2px; /*  */
    background-color: white; /*  */
    content: "";
    transition: width 0.3s ease-out; /*  */
}

.nav__main ul li:hover:after,
.nav__main ul li:focus:after {
    width: 100%; /*  */
}

.nav__main ul li a[class = 'active']:before {
    display: block;
    position: absolute;
    top: 25px;
    left: 0; /*  right:0;,       */
    width: 100%;/*     */
    height: 2px; /*  */
    background-color: white; /*  */
    content: "";
}

@media screen and (min-width: 769px) and (max-width: 1049px) {
    .nav__main ul {
        gap: 20px;
    }
}

@media screen and (min-width: 575px) and (max-width: 768px) {
    .nav__main ul {
        gap: 15px;
    }
}

@media screen and (max-width: 575px) {
    .nav__main {
        display: none;
    }
}
