.header__auth {
    display: flex;
    align-items: center;
    width: 5.3vw;
    height: 50px;
}

.header__auth button {
    display: flex;
    row-gap: 5px;
    align-items: center;
    font-size: 16px;
    width: 100%;
    border: none;
    background-color: inherit;
    position: relative;
    cursor: pointer;
    line-height: 1; /* */
}

.header__auth button:after {
    display: block;
    position: absolute;
    top: 40px;
    left: 0; /*  right:0;,       */
    width: 0;/*     */
    height: 2px; /*  */
    background-color: white; /*  */
    content: "";
    transition: width 0.3s ease-out; /*  */
}

.header__auth button:hover:after,
.header__auth button:focus:after {
    width: 100%; /*  */
}

@media screen and (max-width: 575px){
    .header__auth {
        display: none;
    }
}
